/* Styles for Filler */
.Filler {
  width: 100%;
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Raleway', sans-serif; }
  .Filler .quote {
    width: 75%;
    position: relative;
    color: white;
    text-shadow: 1px 1px 2px #333; }
    .Filler .quote p {
      font-size: 1.3rem;
      font-style: italic;
      text-align: center;
      line-height: 2rem; }
    .Filler .quote span {
      position: absolute;
      right: 0;
      font-style: normal; }
  .Filler.einstein {
    background-image: url("https://raw.githubusercontent.com/anttispitkanen/poetkoe-content/master/images/large/einstein-large.png"); }
  .Filler.adams {
    background-image: url("https://raw.githubusercontent.com/anttispitkanen/poetkoe-content/master/images/large/adams-large.png"); }
  .Filler.nietzsche {
    background-image: url("https://raw.githubusercontent.com/anttispitkanen/poetkoe-content/master/images/large/nietzsche-large.png"); }
  @media screen and (max-width: 800px) {
    .Filler {
      height: 400px; }
      .Filler.einstein {
        background-image: url("https://raw.githubusercontent.com/anttispitkanen/poetkoe-content/master/images/tablet/einstein-tablet.png"); }
      .Filler.adams {
        background-image: url("https://raw.githubusercontent.com/anttispitkanen/poetkoe-content/master/images/tablet/adams-tablet.png"); }
      .Filler.nietzsche {
        background-image: url("https://raw.githubusercontent.com/anttispitkanen/poetkoe-content/master/images/tablet/nietzsche-tablet.png"); } }



