/* Styles for Facebook */
.Facebook {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap-reverse;
      flex-wrap: wrap-reverse;
  -ms-flex-pack: center;
      justify-content: center;
  color: #444;
  padding: 0 20px;
  margin: 30px 0; }
  .Facebook .facebook {
    max-width: 500px;
    padding-left: 40px; }
    .Facebook .facebook h2 {
      text-align: center; }
    .Facebook .facebook p {
      padding: 5px 0; }
    .Facebook .facebook h2 a, .Facebook .facebook p a {
      text-decoration: none;
      color: #7C0487; }
      .Facebook .facebook h2 a:hover, .Facebook .facebook p a:hover {
        text-decoration: underline; }
  @media screen and (max-width: 939px) {
    .Facebook .facebook {
      padding: 0;
      max-width: 750px; } }



